<template>
  <div id="app">
    <Navigator/>
    <div class="content">
      <router-view/>
    </div>
    <div class="tronlink-alert" v-if="!connectToBlockChain">
      <h3>ALERT</h3>
      <p>YOU MUST INSTALL TRONLINK</p>
      <a
        href="https://chrome.google.com/webstore/detail/tronlink/ibnejdfjmmkpcnlpebklmnkoeoihofec"
      >Donwnload here</a>
      <p>YOU MUST LOGIN TRONLINK</p>
      <p>YOU MUST IN MAINNET</p>
    </div>
  </div>
</template>

<script>
import Navigator from "./Components/Navigator";
import { hex2XY } from "@/Utils/smartcontract";

export default {
  name: "app",
  components: { Navigator },
  data() {
    return { contractInstance: {}, connectToBlockChain: false };
  },
  methods: {
    loadPlaces: async function() {
      if (this.$router.currentRoute.name != "visit") {
        let total = (await this.contractInstance
          .totalSupply()
          .call()).toNumber();
        let places = [];
        for (let i = 0; i < total; i++) {
          let tokenID = await this.contractInstance.tokenByIndex(i).call();
          let { x, y } = hex2XY(tokenID._hex);
          let info = await this.contractInstance.getPlaceInfo(tokenID).call();
          let newPlace = {
            name: info[0],
            owner: window.tronWeb.address.fromHex(info[1]),
            location: { x: x, y: y },
            tokenID: tokenID,
            buildUrl: info[2]
          };
          places.push(newPlace);
        }
        this.$store.commit("updatePlaces", places);
      }
      setTimeout(this.loadPlaces, 100);
    }
  },
  async mounted() {
    let self = this;
    let load = async function() {
      console.log("Waiting tronweb load");
      if (window.tronWeb && window.tronWeb.ready) {
        self.connectToBlockChain = true;
        let contractAddress = self.$store.state.contractAddress;
        let contractData = await window.tronWeb.trx.getContract(
          contractAddress
        );
        self.contractInstance = window.tronWeb.contract(
          contractData.abi.entrys,
          contractData.contract_address
        );
        self.$store.commit("updateContractInstance", self.contractInstance);
        self.loadPlaces();
      } else {
        setTimeout(load, 100);
      }
    };
    setTimeout(load, 100);
  }
};
</script>

