var ndarray = require("ndarray")
var voxel = require("voxel")

//@dev Using vox.js
var voxjs = require("vox.js");
var parser = new voxjs.Parser();
var loadVoxToNDArray = function (arrayBuffer) {
    var uint8Array = new Uint8Array(arrayBuffer);
    parser.parseUint8Array(uint8Array).then(function (voxelData) {
        // console.log(voxelData);
        voxelData.voxels; // voxel position and color data
        voxelData.size; // model size
        voxelData.palette; // palette data

        var resolutionX = voxelData.size.x;
        var resolutionY = voxelData.size.y;
        var resolutionZ = voxelData.size.z;
        var voxArr = ndarray(new Int32Array(resolutionX * resolutionY * resolutionZ), [resolutionX, resolutionY, resolutionZ])
        voxelData.voxels.forEach(vox => {
            voxArr.set(vox.x, vox.y, vox.z, vox.colorIndex);
        })
        return voxArr;
    });
}
var loadUrlVoxToNDArray = function (url,callBack) {
    // console.log(url);
    parser.parse(url).then(function (voxelData) {
        // console.log(voxelData);
        voxelData.voxels; // voxel position and color data
        voxelData.size; // model size
        voxelData.palette; // palette data

        var resolutionX = voxelData.size.x;
        var resolutionY = voxelData.size.y;
        var resolutionZ = voxelData.size.z;
        var voxArr = ndarray(new Int32Array(resolutionX * resolutionY * resolutionZ), [resolutionX, resolutionY, resolutionZ])
        voxelData.voxels.forEach(vox => {
            voxArr.set(vox.y, vox.z, vox.x, vox.colorIndex);
        })
        callBack(voxArr);
    });
}

var getUv = function (index, uvs, v1, v2, v3, v4) {
    var heightTexture = 1;
    var widthTexture = 256;
    var tileX = (index - 1) % widthTexture;
    var tileY = Math.floor((index - 1) / widthTexture);
    var uPerc = 1 / widthTexture;
    var vPerc = 1 / heightTexture;
    var umin = uPerc * tileX;
    var umax = uPerc * (tileX + 1);
    var vmin = vPerc * tileY;
    var vmax = vPerc * (tileY + 1);

    var padding = 0.49;//KHanhND: Not over 0.5 because padding 0.5 nothing to padding
    var vPadding = padding * vPerc;
    var uPadding = padding * uPerc;
    umin += uPadding;
    vmin += vPadding;
    umax -= uPadding;
    vmax -= vPadding;

    uvs[v1 * 2] = umin;
    uvs[v1 * 2 + 1] = vmin;
    uvs[v2 * 2] = umax;
    uvs[v2 * 2 + 1] = vmin;
    uvs[v3 * 2] = umax;
    uvs[v3 * 2 + 1] = vmax;
    uvs[v4 * 2] = umin;
    uvs[v4 * 2 + 1] = vmax;
}
var loadVoxel = function (arrayBuffer) {
    var voxArr = loadVoxToNDArray(arrayBuffer);
    var greedy = voxel.meshers.greedy(voxArr.data, voxArr.shape);
    // var greedy = voxel.meshers.stupid(voxArr.data, voxArr.shape);

    // console.log(greedy);
    var incides = [];
    var positions = [];

    greedy.vertices.forEach(e => { positions.push(e[0]); positions.push(e[1]); positions.push(e[2]) })
    var uvs = new Array(positions.length * 2 / 3);
    greedy.faces.forEach(e => {
        incides.push(e[1]);
        incides.push(e[0]);
        incides.push(e[2]);
        incides.push(e[3]);
        incides.push(e[2]);
        incides.push(e[0]);
        getUv(e[4], uvs, e[0], e[1], e[2], e[3]);
    });

    // console.log(incides);
    // console.log(positions);
    // console.log(uvs);

    return { incides, positions, uvs }
}
var loadVoxelUrl = function (url,callBack) {
    // console.log(url);
    loadUrlVoxToNDArray(url,function(voxArr){
        var greedy = voxel.meshers.greedy(voxArr.data, voxArr.shape);
        // var greedy = voxel.meshers.stupid(voxArr.data, voxArr.shape);
    
        // console.log(voxArr.shape);
        var incides = [];
        var positions = [];
    
        greedy.vertices.forEach(e => { positions.push(e[0]); positions.push(e[1]); positions.push(e[2]) })
        var uvs = new Array(positions.length * 2 / 3);
        greedy.faces.forEach(e => {
            incides.push(e[1]);
            incides.push(e[0]);
            incides.push(e[2]);
            incides.push(e[3]);
            incides.push(e[2]);
            incides.push(e[0]);
            getUv(e[4], uvs, e[0], e[1], e[2], e[3]);
        });
    
        // console.log(incides);
        // console.log(positions);
        // console.log(uvs);
        let sizeVox=voxArr.shape;
        callBack({ incides, positions, uvs, sizeVox });

    });
    
}
export  {loadVoxel,loadVoxelUrl}



