<template>
  <div>
    <div class="title-page">List Place</div>
    <div class="list-place">
      <div class="place" v-for="(place,key) in places" :key="key">
        <div class="info">
          <div>{{place.name}}</div>
          <div>Location: x:{{place.location.x}}, y:{{place.location.y}}</div>
          <div>Building link (support vox):</div>
          <a>
            <small>{{place.buildUrl.substr(0,15)+"..."+place.buildUrl.substr(place.buildUrl.length-10,10)}}</small>
          </a>
          <div class="place-control">
            <VueButton
              class="primary"
              icon-left="search"
              @click="$router.push({ name: 'visit' });"
            >Visit</VueButton>
            <VueButton
              class="primary"
              icon-left="create"
              @click="$router.push({ name: 'edit',query: { x:place.location.x,y:place.location.y } })"
            >Edit</VueButton>
          </div>
        </div>
        <TronVox :url="place.buildUrl" class="preview"></TronVox>
        <!-- <div class="preview">hihihi</div> -->
      </div>
    </div>
  </div>
</template>

<script>
import TronVox from "@/Components/TronVox";

export default {
  components: { TronVox },
  data() {
    return {};
  },
  computed: {
    places: function() {
      return window.tronWeb != undefined
        ? this.$store.state.places.filter(
            e => e.owner == window.tronWeb.defaultAddress.base58
          )
        : [];
    }
  }
};
</script>

<style>
.title-page {
  text-align: center;
  font-size: xx-large;
  padding: 20px;
}
.list-place {
  margin: 10px auto;
  padding: 10px;
  display: flex;
  flex-wrap:wrap;
}
.place {
  display: grid;
  grid-template-columns: auto 200px;
  border: 1px solid #42ba8f;
  /* margin: 5px; */
  border-radius: 1px;
  padding: 5px;
  margin: 5px auto;
}
.info {
  margin: auto;
}
.preview {
  width: 190px;
  height: 190px;
  border: 1px dashed;
  border-radius: 1px;
  padding: 5px;
}
div.preview canvas.tron-vox {
  height: 190px;
  border-radius: 1px;
}
.place-control {
  padding: 5px;
  margin: 0px auto;
  width: fit-content;
}
.place-control button {
  margin: 5px;
}
</style>
