<template>
  <div>
    <div id="header" v-if="this.$router.currentRoute.name!='go'">
      <img id="logo" src="@/assets/logo.png">
    </div>
    <div class="navigator">
      <div
        class="menu"
        v-for="(menu,key) in menus"
        :key="key"
        @click="$router.push({ name: menu.routerName });"
      >{{menu.name}}</div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      menus: [
        { name: "Home", routerName: "index" },
        { name: "Visit", routerName: "visit" },
        { name: "Buy place", routerName: "buy-place" },
        { name: "My place", routerName: "my-place" }
      ]
    };
  },
  methods: {}
};
</script>

<style>
#header * {
  margin: 0 auto;
}
#header {
    background: #eee;
}
#logo {
  height: 50px;
  display: block;
  transition: all 0.3s cubic-bezier(1, -0.76, 0.58, 1);
  padding: 10px;
  filter: brightness(100%) drop-shadow(0px 0px 10px #ffffffa1);
}
#logo:hover {
  filter: brightness(150%) drop-shadow(0px 0px 10px #ffffffa1);
}
.navigator {
  display: flex;
  justify-content: center;
  background: #eee;
  padding: 5px;
}
.menu {
  padding: 2px 5px 2px 5px;
  border: 1px solid #001826;
  border-radius: 1px;
  margin: 0 5px;
  cursor: pointer;
  transition: all 0.2s ease;
}
.menu:hover {
  background:  #990000;
  color: #fff;
}
</style>
