
<template>
  <div>
    <canvas
      id="TronVRWorld"
      class="tron-vox"
      :style="{width:gameWidth+'px',height:gameHeight+'px'}"
    ></canvas>
  </div>
</template>

<script>
import { loadVoxelUrl } from "@/Utils/loadvoxel";
import Player from "@/GameObject/Player";

export default {
  data() {
    return {
      gameWidth: window.innerWidth - 10,
      gameHeight: window.innerHeight - 10,
      baseUrl: this.$store.state.baseUrl,
      scene: {},
      buildMaterial: {}
    };
  },
  computed: {
    places: function() {
      return this.$store.state.places;
    }
  },
  mounted() {
    var canvas = document.getElementById("TronVRWorld");

    var engine = new BABYLON.Engine(canvas, true, {
      preserveDrawingBuffer: true,
      stencil: true
    });
    this.scene = this.createScene(engine, canvas);
    let self = this;
    engine.runRenderLoop(function() {
      if (self.scene) {
        self.scene.render();
      }
    });
    let waitPlacesLoaded = function() {
      if (self.places != undefined) {
        self.loadWorld(0, 0);
        console.log("world loaded");
      } else {
        setTimeout(waitPlacesLoaded, 100);
      }
    };
    setTimeout(waitPlacesLoaded, 100);
  },
  methods: {
    createScene: function(engine, canvas) {
      var scene = new BABYLON.Scene(engine);
      scene.debugLayer.show();
      scene.clearColor = new BABYLON.Color4(0, 0, 1, 0.3);
      scene.collisionsEnabled = true;
      scene.workerCollisions = true;

      scene.fogMode = BABYLON.Scene.FOGMODE_EXP2;
      scene.fogDensity = 0.003;
      scene.fogColor = new BABYLON.Color3(0.8, 0.83, 0.8);

      scene.enablePhysics(
        new BABYLON.Vector3(0, -40, 0),
        new BABYLON.CannonJSPlugin(true, 100)
      );

      const player = new Player(scene, new BABYLON.Vector3(0, 10, 0));

      //Camera
      var cameraTopDown = new BABYLON.FreeCamera(
        "cameraTopDown",
        new BABYLON.Vector3(0, 200, 0),
        scene
      );

      player.camera.viewport = new BABYLON.Viewport(0, 0, 1, 1);
      cameraTopDown.viewport = new BABYLON.Viewport(0, 0, 0.2, 0.2);

      scene.activeCameras.push(player.camera);
      scene.activeCameras.push(cameraTopDown);
      // scene.cameraToUseForPointers = player.camera;
      // This attaches the camera to the canvas
      //   camera.attachControl(canvas, true);

      var light = new BABYLON.HemisphericLight(
        "light",
        new BABYLON.Vector3(5, 5, 1),
        scene
      );
      window.SCEN = scene;
      light.specular = new BABYLON.Color3(0.5, 0.5, 0.5);
      light.specularPower = 0;
      light.diffuse = new BABYLON.Color3(0.843, 0.933, 0.922);
      light.intensity = 0.75;
      light.groundColor = new BABYLON.Color3(0.5, 0.5, 0.5);

      var matGrid = new BABYLON.GridMaterial("matGrid", scene);
      matGrid.gridRatio = 1;
      matGrid.majorUnitFrequency = 1;
      matGrid.mainColor = new BABYLON.Color3(1, 1, 1);
      matGrid.lineColor = new BABYLON.Color3(0.8, 0.8, 0.8);

      //   let skyMat = new BABYLON.SkyMaterial("skyMaterial", scene);
      //   skyMat.backFaceCulling = false;
      //   skyMat.inclination = 0;
      //   var skybox = BABYLON.Mesh.CreateBox("skyBox", 8000, scene);
      //   skybox.material = skyMat;

      var ground = BABYLON.Mesh.CreateGround(
        "GridGround",
        16000,
        16000,
        1,
        scene
      );
      ground.material = matGrid;
      // ground.checkCollisions = true;
      ground.position = new BABYLON.Vector3(0, 0, 0);
      ground.physicsImpostor = new BABYLON.PhysicsImpostor(
        ground,
        BABYLON.PhysicsImpostor.BoxImpostor,
        { mass: 0, restitution: 0 },
        scene
      );

      var groundLayout = ground.clone("LayoutGround");
      groundLayout.position.y += 0.05;

      var LayoutGroundMat = new BABYLON.StandardMaterial(
        "LayoutGroundMat",
        scene
      );
      LayoutGroundMat.diffuseTexture = new BABYLON.Texture(
        this.baseUrl + "textures/ground.png",
        scene
      );
      console.log("set vscale");

      LayoutGroundMat.diffuseTexture.vScale = 100;
      LayoutGroundMat.diffuseTexture.uScale = 100;
      LayoutGroundMat.alpha = 0.5;

      groundLayout.material = LayoutGroundMat;

      this.buildMaterial = new BABYLON.StandardMaterial("VoxelMat", scene);
      this.buildMaterial.diffuseTexture = new BABYLON.Texture(
        this.baseUrl + "textures/voxel.png",
        scene
      );
      this.buildMaterial.backFaceCulling = true;

      //   var postProcess = new BABYLON.ImageProcessingPostProcess(
      //     "processing",
      //     1.0,
      //     scene.activeCamera
      //   );
      //   postProcess.vignetteWeight = 5;
      //   postProcess.vignetteStretch = 1;
      //   postProcess.vignetteColor = new BABYLON.Color4(0, 0, 0, 0);
      //   postProcess.vignetteEnabled = true;
      scene.registerBeforeRender(() => {
        const force = 2 * engine.getDeltaTime();
        if (player.isOnGround()) {
          let velocity = new BABYLON.Vector3(0, 0, 0);
          if (keys[32]) {
            player.body.physicsImpostor.applyImpulse(
              new BABYLON.Vector3(0, 1200, 0),
              player.body.getAbsolutePosition()
            );
          } else {
            if (keys[87]) {
              velocity = velocity.add(player.getFront());
            }
            if (keys[83]) {
              velocity = velocity.add(player.getBack());
            }
            if (keys[65]) {
              velocity = velocity.add(player.getLeft());
            }
            if (keys[68]) {
              velocity = velocity.add(player.getRight());
            }
            player.body.physicsImpostor.setLinearVelocity(
              velocity.normalize().multiplyByFloats(force, force, force)
            );
          }
        }
        cameraTopDown.position.x = player.body.position.x;
        cameraTopDown.position.z = player.body.position.z;
        cameraTopDown.setTarget(player.body.position);
        cameraTopDown.rotation.z = 9.43;
      });
      /* add mousemove listener, change camera lookat */
      const mousemove = e => {
        player.head.rotate(
          BABYLON.Axis.X,
          e.movementY / 1000,
          BABYLON.Space.LOCAL
        );
        player.head.rotate(
          BABYLON.Axis.Y,
          e.movementX / 1000,
          BABYLON.Space.WORLD
        );
      };
      document.addEventListener(
        "pointerlockchange",
        function() {
          if (document.pointerLockElement === canvas) {
            document.addEventListener("mousemove", mousemove, false);
          } else {
            document.removeEventListener("mousemove", mousemove, false);
          }
        },
        false
      );
      /* add key listener */
      const keys = {};
      window.addEventListener("keydown", e => {
        keys[e.keyCode] = true;
        if (e.keyCode === 9) e.preventDefault();
      });
      window.addEventListener("keyup", e => {
        keys[e.keyCode] = false;
        if (e.keyCode === 9) e.preventDefault();
      });
      /* requestPointerLock */
      scene.onPointerDown = () => canvas.requestPointerLock();
      window.addEventListener("resize", () => {
        // this.gameWidth = window.innerWidth;
        // this.gameHeight = window.innerHeight;
        engine.resize();
      });
      scene.registerBeforeRender(() => {});
      return scene;
    },
    loadVoxel: function(url, voxMeshToLoad) {
      if (url != null && url != "") {
        loadVoxelUrl(url, function({ incides, positions, uvs }) {
          var vertexData = new BABYLON.VertexData();
          vertexData.indices = incides;
          vertexData.positions = positions;
          vertexData.uvs = uvs;
          vertexData.applyToMesh(voxMeshToLoad);
        });
      }
    },
    loadWorld: function(userX, userY) {
      console.log(userX, userY);
      let listPlacesToView = this.places;
      console.log(this.places);
      listPlacesToView.forEach(place => {
        let voxMesh = new BABYLON.Mesh(
          "Build-" + place.location.x + "-" + place.location.y,
          this.scene
        );
        voxMesh.material = this.buildMaterial;
        voxMesh.position = new BABYLON.Vector3(
          17 + place.location.x * 160,
          0,
          17 + place.location.y * 160
        );
        if (place.buildUrl == "") {
          this.loadVoxel(this.$store.state.undercontructorFile, voxMesh);
        } else {
          this.loadVoxel(place.buildUrl, voxMesh);
        }
      });
    }
  }
};
</script>

<style>
canvas#TronVRWorld {
  margin: 5px auto;
  display: block;
}
</style>
