<template>
  <dir>
    <div class="title-page">Buy Place</div>
    <div class="map">
      <div class="row" v-for="x in maxX" :key="x">
        <div
          class="place-slot"
          v-for="y in maxY"
          :key="y"
          :class="{buyed:buyedArr[x][y]!=false}"
          :style="{borderTop: x==50?'1px solid #f00':0,borderLeft:y==50?'1px solid #f00':0}"
          :id="x+'-'+y"
          @mousemove="show(x,y)"
          @click="buy(x,y)"
        ></div>
      </div>
    </div>
    <div
      class="status"
      :style="{ left: statusPosition.left + 'px', top: statusPosition.top+ 'px' }"
    >
      <div>
        <small>Location:</small>
        ({{currentPlacePos.x}}:{{currentPlacePos.y}})
      </div>
      <div v-if="placeStatus">
        <div>
          <small>Owner:</small>
          {{placeStatus.owner}}
        </div>
        <div>
          <small>Name:</small>
          {{placeStatus.name}}
        </div>
      </div>
      <div v-else>You can buy this</div>
    </div>
  </dir>
</template>

<script>
import { convertXY2Hex } from "@/Utils/smartcontract";

export default {
  data() {
    return {
      maxX: 100,
      maxY: 100,
      currentPlacePos: { x: 0, y: 0 },
      placeStatus: "",
      statusPosition: {
        left: 0,
        top: 0
      }
    };
  },
  computed: {
    buyedArr: function() {
      let buyedArr = [...Array(101)].map(() => Array(101).fill(false));
      this.buyedPlace.forEach(place => {
        buyedArr[place.location.x + 50][place.location.y + 50] = {
          owner: place.owner,
          name: place.name
        }; //center world convert 0-10 to -50,50 array
      });
      return buyedArr;
    },
    buyedPlace: function() {
      return this.$store.state.places;
    },
    contractInstance: function() {
      return this.$store.state.contractInstance;
    }
  },
  watch: {
    buyedPlace: function() {
      this.maxX = 0;
      this.maxY = 0;
      this.maxX = 100;
      this.maxY = 100;
    }
  },
  methods: {
    show: function(x, y) {
      this.placeStatus = this.buyedArr[x][y];
      this.currentPlacePos.x = x - 50;
      this.currentPlacePos.y = y - 50; //convert 0-10 to -50,50 array
    },
    buy: async function(x, y) {
      if (this.placeStatus == false) {
        try {
          await this.contractInstance
            .buy(convertXY2Hex(x - 50, y - 50))
            .send({ callValue: 123000000 });
          this.$swal("Sent buy order to blockchain");
        } catch (e) {
          this.$swal({
            type: "error",
            title: "Oops...",
            text: e.toString()
          });
        }
      } else {
        this.$swal({
          type: "error",
          title: "Oops...",
          text: "This place buyed!"
        });
      }
    }
  },
  async mounted() {
    Element.prototype.documentOffsetTop = function() {
      return (
        this.offsetTop +
        (this.offsetParent ? this.offsetParent.documentOffsetTop() : 0)
      );
    };
    setTimeout(window.scrollTo(0, top), 2000);

    var top =
      document.getElementById("50-50").documentOffsetTop() -
      window.innerHeight / 2;
    let self = this;
    let onMouseUpdate = function(e) {
      self.statusPosition.left = e.pageX + 20;
      self.statusPosition.top = e.pageY;
    };
    document.addEventListener("mousemove", onMouseUpdate, false);
  }
};
</script>

<style scoped>
.map {
  display: grid;
  margin: 10px auto;
  width: fit-content;
  height: 500px;
  overflow: scroll;
  border: 1px solid;
  background: #ccc;
  box-shadow: 0px 0px 20px inset;
}
.row {
  width: 100%;
  height: 12px;
  margin: 0px;
  padding: 0px;
}
.place-slot {
  width: 10px;
  height: 10px;
  border: 1px solid #eee;
  display: block;
  float: left;
}
.place-slot:hover {
  /* border: 0.05px solid #f00; */
  background: #f00;
  box-shadow: 0px 0px 5px #000;
}
.buyed {
  background: #0f0;
  /* border: 0.05px solid #f00; */
}
.status {
  z-index: 999;
  position: absolute;
  border: 1px solid #000;
  box-shadow: 0px 0px 10px #000;
  border-radius: 5px;
  padding: 5px;
  background: #eee;
}
</style>
