<template>
  <div>
    <div class="title-page">Edit Place</div>
    <!-- <VueTabs :tab-id.sync="tabId" group-class="primary" animate class="contract-tabs">
      <VueTab id="over-view" label="Building" icon="home"> -->
        <div class="content-page">
          <div class="over-view">
            <div @click="showEditName=!showEditName">
              Name: {{place.name}}
              <small>Click to edit</small>
            </div>
            <div class="edit-buildurl-panel" v-if="showEditName">
              <VueInput
                v-model="nameInput"
                placeholder="Type your place name"
                class="loader-voxurl"
              />
              <VueButton class="danger" icon-left="save" @click="saveName()">Save to BlockChain</VueButton>
            </div>
            <div>Location: x:{{place.location.x}}, y:{{place.location.y}}</div>
            <div>Owner: {{place.owner}}</div>
            <div>
              TokenID:
              <small>{{place.tokenID.toString()}}</small>
            </div>
            <div>Building link (support vox):</div>
            <a @click="showEditBuildUrl=!showEditBuildUrl">
              <small>{{place.buildUrl.substr(0,30)+"..."+place.buildUrl.substr(place.buildUrl.length-20,20)}}</small>
            </a>
            <div class="edit-buildurl-panel" v-if="showEditBuildUrl">
              <VueInput
                v-model="urlInput"
                placeholder="Type your url contain vox file"
                class="loader-voxurl"
              />
              <VueButton class="primary" icon-left="search" @click="load()">Load vox</VueButton>
              <VueButton class="danger" icon-left="save" @click="saveUri()">Save to BlockChain</VueButton>
            </div>
          </div>
          <div class="place-preview">
            <div>YOUR BUILDING</div>
            <TronVox :url="urlTronVoxLoad"></TronVox>
          </div>
        </div>
      <!-- </VueTab>
      <VueTab id="items" label="Items" icon="extension">
        <div class="content-page">
          <div class="over-view">
            <div
              class="item"
              v-for="(item,key) in place.items"
              :key="key"
              @click="selectedItem=item"
            >{{key}} - {{typeItems[item.typeItemID].name}}</div>
          </div>
          <div class="place-preview">
            <div>Item preview</div>
            <TronVox
              v-if="selectedItem!=undefined"
              :url="typeItems[selectedItem.typeItemID].dataUrl"
            ></TronVox>
          </div>
        </div>
      </VueTab>
    </VueTabs> -->
  </div>
</template>

<script>
import TronVox from "@/Components/TronVox";

export default {
  components: { TronVox },
  props: { location: Object },
  data() {
    return {
      selectedItem: undefined,
      tabId: "over-view",
      showEditBuildUrl: true,
      showEditName: false,
      urlInput: "",
      urlTronVoxLoad: "",
      nameInput: ""
    };
  },
  computed: {
    typeItems: function() {
      return this.$store.state.typeItems;
    },
    place: function() {
      return this.$store.state.places.filter(
        place =>
          place.location.x == this.location.x &&
          place.location.y == this.location.y
      )[0];
    }
  },
  methods: {
    load: function() {
      this.urlTronVoxLoad = this.urlInput;
    },
    saveUri: async function() {
      try {
        this.urlTronVoxLoad = this.urlInput;
        await this.$store.state.contractInstance
          .setContentURI(this.place.tokenID, this.urlInput)
          .send();
        this.$swal("Sent edit uri to blockchain");
      } catch (e) {
        this.$swal({
          type: "error",
          title: "Oops...",
          text: e.toString()
        });
      }
    },
    saveName: async function() {
      try {
        await this.$store.state.contractInstance
          .setName(this.place.tokenID, this.nameInput)
          .send();
        this.$swal("Sent new name to blockchain");
      } catch (e) {
        this.$swal({
          type: "error",
          title: "Oops...",
          text: e.toString()
        });
      }
    }
  },
  mounted() {
    this.urlTronVoxLoad = this.place.buildUrl;
    this.urlInput = this.place.buildUrl;
    this.nameInput = this.place.name;
  }
};
</script>

<style>
.over-view {
  margin: 10px;
  padding: 10px;
  border: 1px #42b983 solid;
  border-radius: 1px;
}
.title-page {
  text-align: center;
  font-size: large;
  padding: 20px;
}
div.over-view > * {
  margin: 10px;
}
.place-preview {
  padding: 10px;
  text-align: center;
  border: 1px solid #fff;
  border-radius: 1px;
  box-shadow: 0px 0px 10px #3778b1;
  transition: all 0.3s ease;
}
.place-preview:hover {
  box-shadow: 0px 0px 100px #3778b1;
}
canvas.tron-vox {
  height: 300px;
  border-radius: 1px;
}
.content-page {
  display: grid;
  grid-template-columns: auto 300px;
  margin: 10px auto;
  width: 800px;
}
.edit-buildurl-panel {
  border: 1px dashed #42b983;
  border-radius: 1px;
  padding: 10px;
}
.vue-ui-input.loader-voxurl.loader-voxurl {
  width: 100%;
}
.item {
  border: 1px dashed #42b983;
  padding: 10px;
  border-radius: 1px;
  transition: all 0.5s ease;
}
.item:hover {
  box-shadow: 0px 0px 10px #42b983;
  border: 1px dashed #42b983;
  padding: 10px;
  border-radius: 1px;
}
</style>
