<template>
  <div>
    <canvas :id="name" class="tron-vox"></canvas>
  </div>
</template>

<script>
import { loadVoxelUrl } from "@/Utils/loadvoxel";

export default {
  data() {
    return {
      baseUrl: this.$store.state.baseUrl,
      name: "TronVox-" + Math.floor(Math.random() * 100),
      scene: {},
      voxMesh: {},
      camera: {}
    };
  },
  props: {
    url: String
  },
  watch: {
    url: function() {
      this.loadVoxel();
    }
  },
  mounted() {
    var canvas = document.getElementById(this.name);

    var engine = new BABYLON.Engine(canvas, true, {
      preserveDrawingBuffer: true,
      stencil: true
    });
    this.scene = this.createScene(engine, canvas);
    let self = this;
    engine.runRenderLoop(function() {
      if (self.scene) {
        self.scene.render();
      }
    });
  },
  methods: {
    createScene: function(engine, canvas) {
      // This creates a basic Babylon Scene object (non-mesh)
      var scene = new BABYLON.Scene(engine);
      console.log(scene);
      //   scene.debugLayer.show();
      scene.clearColor = new BABYLON.Color4(0, 0, 1, 0.3);
      scene.collisionsEnabled = true;
      //   scene.fogMode = BABYLON.Scene.FOGMODE_EXP2;
      //   scene.fogDensity = 0.003;
      //   scene.fogColor = new BABYLON.Color3(0.8, 0.83, 0.8);

      this.camera = new BABYLON.ArcRotateCamera(
        "camera1",
        0,
        0,
        0,
        new BABYLON.Vector3(63 + 17, 126, 63 + 17),
        scene
      );
      this.camera.setPosition(new BABYLON.Vector3(-150, 10, -150));
      this.camera.attachControl(canvas, true);
      this.camera.checkCollisions = true;

      //   // This attaches the camera to the canvas
      //   camera.attachControl(canvas, true);
      var light = new BABYLON.HemisphericLight(
        "light",
        new BABYLON.Vector3(5, 5, 1),
        scene
      );

      light.specular = new BABYLON.Color3(0.5, 0.5, 0.5);
      light.specularPower = 0;
      light.diffuse = new BABYLON.Color3(0.843, 0.933, 0.922);
      light.intensity = 0.75;
      light.groundColor = new BABYLON.Color3(0.5, 0.5, 0.5);

      var matGrid = new BABYLON.GridMaterial("matGrid", scene);
      matGrid.gridRatio = 1;
      matGrid.majorUnitFrequency = 1;
      matGrid.mainColor = new BABYLON.Color3(1, 1, 1);
      matGrid.lineColor = new BABYLON.Color3(0.8, 0.8, 0.8);

      let skyMat = new BABYLON.SkyMaterial("skyMaterial", scene);
      skyMat.backFaceCulling = false;
      skyMat.inclination = 0;
      var skybox = BABYLON.Mesh.CreateBox("skyBox", 1260, scene);
      skybox.material = skyMat;

      var ground = BABYLON.Mesh.CreateGround(
        "GridGround",
        1260,
        1260,
        1,
        scene
      );
      ground.material = matGrid;
      ground.checkCollisions = true;
      ground.position = new BABYLON.Vector3(0, 0, 0);

      //   var groundLayout = ground.clone("LayoutGround");
      //   groundLayout.position.y += 0.05;
      //   var LayoutGroundMat = new BABYLON.StandardMaterial(
      //     "LayoutGroundMat",
      //     scene
      //   );
      //   LayoutGroundMat.diffuseTexture = new BABYLON.Texture(
      //     this.baseUrl+"textures/ground.png",
      //     scene
      //   );
      //   LayoutGroundMat.diffuseTexture.vScale = 100;
      //   LayoutGroundMat.diffuseTexture.uScale = 100;
      //   LayoutGroundMat.alpha = 0.5;

      //   groundLayout.material = LayoutGroundMat;

      var mat = new BABYLON.StandardMaterial("VoxelMat", scene);
      mat.diffuseTexture = new BABYLON.Texture(
        this.baseUrl + "textures/voxel.png",
        scene
      );
      // mat.specularColor = BABYLON.Color3.Black();
      // mat.emissiveColor = BABYLON.Color3.White();
      mat.backFaceCulling = true;

      this.voxMesh = new BABYLON.Mesh("boxes", scene);
      this.voxMesh.material = mat;
      this.voxMesh.position = new BABYLON.Vector3(17, 0, 17);

      this.loadVoxel();

      //   var postProcess = new BABYLON.ImageProcessingPostProcess(
      //     "processing",
      //     1.0,
      //     scene.activeCamera
      //   );
      //   postProcess.vignetteWeight = 5;
      //   postProcess.vignetteStretch = 1;
      //   postProcess.vignetteColor = new BABYLON.Color4(0, 0, 0, 0);
      //   postProcess.vignetteEnabled = true;
      window.addEventListener("resize", () => engine.resize());
      scene.registerBeforeRender(() => {
        this.camera.alpha += 0.001;
      });
      return scene;
    },
    loadVoxel: function() {
      if (this.url != null && this.url != "") {
        let self = this;
        loadVoxelUrl(this.url, function({ incides, positions, uvs, sizeVox }) {
          var vertexData = new BABYLON.VertexData();
          vertexData.indices = incides;
          vertexData.positions = positions;
          vertexData.uvs = uvs;
          vertexData.applyToMesh(self.voxMesh);
          self.camera.setTarget(
            new BABYLON.Vector3(
              sizeVox[0] / 2 + 17,
              sizeVox[1] / 2,
              sizeVox[2] / 2 + 17
            )
          );
          self.camera.setPosition(
            new BABYLON.Vector3(-sizeVox[0] - 10, 10, -sizeVox[2] - 10)
          );
        });
      }
    }
  }
};
</script>

<style>
canvas {
  width: 100%;
}
</style>
