import Vue from 'vue'
import Router from 'vue-router'
import EditPlace from './Components/EditPlace'
import ListPlace from './Components/ListPlace'
import BuyPlace from './Components/BuyPlace'
import VisitWorld from './Components/VisitWorld'
import HomePage from './Components/HomePage'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: "/",
      name: 'index',
      component: HomePage
    },
    {
      path: '/edit',
      name: 'edit',
      component: EditPlace,
      props: (route) => ({ location:{x: route.query.x, y: route.query.y }})
    },
    {
      path: '/my-place',
      name: 'my-place',
      component: ListPlace,
    },
    {
      path: '/buy-place',
      name: 'buy-place',
      component: BuyPlace,
    },
    {
      path: '/visit',
      name: 'visit',
      component: VisitWorld,
    }
  ],
})